*{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}
.MAIN{
    display: grid;
    grid-template-columns:  700px 300px ;
}

.mainshirt1
{
    margin-top: 100px;
    margin-left: 100px;
}
.shirt3{
    margin-top: 104px;
    margin-left: 100px;
    padding-left: 40px;
    padding-top: 40px;
    width: 300px;
    min-height: 400px;
    box-shadow: rgba(0, 0, 0, 0.4) 0px 2px 4px, rgba(0, 0, 0, 0.3) 0px 7px 13px -3px, rgba(0, 0, 0, 0.2) 0px -3px 0px inset;
}

.shirt1{
   display: flex;
   box-shadow: rgba(0, 0, 0, 0.4) 0px 2px 4px, rgba(0, 0, 0, 0.3) 0px 7px 13px -3px, rgba(0, 0, 0, 0.2) 0px -3px 0px inset;
}
.shirt2{
    display: flex;
    margin-top: 100px;
    box-shadow: rgba(0, 0, 0, 0.4) 0px 2px 4px, rgba(0, 0, 0, 0.3) 0px 7px 13px -3px, rgba(0, 0, 0, 0.2) 0px -3px 0px inset;
}

.des1{
    margin-top: 30px;
    margin-left: 60px;
}

.des2{
    margin-top: 30px;
    margin-left: 60px;
}

.btn{
    width: 40px;
    height: 50px;
    font-size: 30px;
    background-color: #DE3163 ;
    border: 2px solid black;
    margin-left: 20px;
    border-radius: 15px;
}

.btn1{
    width: 40px;
    height: 50px;
    font-size: 30px;
    background-color:  #f0cef0;
    border: 2px solid black;
    margin-left: 20px;
    border-radius: 15px;
}
/* .btn:hover{
    background-color:  #C084CC;
    cursor: pointer;
    opacity: 1;
} */

::placeholder{
    font-size: 17px;
}

.btns{
    font-size: 30px;
    margin-left: 20px;
}
.pri{
    font-size: 25px;
    margin-left: 14px;
  
}

.img1, .img2{
    display: flex;
}
.img1 h3, .img2 h3{
      margin-top: 50px;
}
.shirt3 .total span
{
      font-size: 25px
}

.shirt3 .total button
{
    width: 110px;
    height: 40px;
    background-color:  #788FDE;
    border: 2px solid black;
    border-radius: 15px;
    font-size: 14px;
    margin-top: 30px;
}

.shirt3 .discount input{
    width: 240px;
    height: 40px;
    margin-top: 30px;
    border: 3px solid black;
    border-radius: 10px;
}

.shirt3 .discount button{

    width: 110px;
    height: 40px;
    background-color:  #788FDE;
    border: 2px solid black;
    border-radius: 15px;
    font-size: 14px;
    margin-top: 30px;
}

.shirt3 .discount h4{
    font-size: 20px;
}
.shirt3 .discount span{
    font-size: 20px;
}

@media (max-width:998px)
{
    .MAIN{
        display: block;
    }
    .shirt1,.shirt2{
        margin-left: -60px;
        display: block;
        margin-right: 40px;
        min-height: 450px;
        width: 320px;
    }

    .mainshirt3{
        margin-left: -60px;
        margin-right: 50px;
        max-height: 400px;
        margin-bottom: 40px;
    }


    
}